import React from 'react';
import {Switch, Route, Redirect} from "react-router-dom";
import loginPage from "./loginPage/loginPage";
import registerPage from "./registerPage/registerPage";
import mainPage from "./mainPage/mainPage";
import logo from "./guts/logo96.png"
import "./guts/styles.css"

class App extends React.Component{
    render() {
        return (
            <div className="App">
                <Header />
                <Switch>
                    <Switch>
                        <Route exact path="/authorisation" component={loginPage}/>
                        <Route exact path="/registration" component={registerPage}/>
                        <Route path="/" component={mainPage}/>
                    </Switch>
                </Switch>
            </div>
        );
    }
}

const Header = ()=>{
    return(
        <header className="header">
            <a href="https://se.ifmo.ru" target="_blank" rel="noopener noreferrer">
                <img className="se-logo" src={logo} alt="The SE faculty logo"/>
            </a>
            <div className="title-container">
                <h2 className="pointify-title">Лабораторная работа №4 - Вариант 700000</h2>
                <h3 className="pointify-author">
                    Created by-
                    <a className="author" href="https://github.com/TuzSeRik"
                       target="_blank" rel="noopener noreferrer">
                       Dmitriy Solodovnikov</a>
                </h3>
            </div>
        </header>
    )
};
export default App;